.wea-locale-provider-set {
	width: 100%;
	height: 100%;
	padding: 10px;
}

.wea-locale-provider-set .ant-row {
	height: 100%;
}

.wea-locale-provider-set .ant-row>div {
	height: 100%;
	padding-top: 40px;
}

.wea-locale-provider-set .ant-table {
	border-radius: 0;
	border: 0;
}

.wea-locale-provider-set .wea-input-focus,
.wea-locale-provider-set .ant-select {
	width: 200px;
	position: absolute;
	top: 0;
}

.wea-locale-provider-set .ant-select-selection {
	border-radius: 0;
}

.wea-locale-provider-set .wea-locale-provider-set-save{
	position: absolute;
	left: 220px;
	top: 0;
    border-radius: 2px;
}
.wea-locale-provider-set .wea-locale-provider-set-table-wrap {
	width: 100%;
	height: 100%;
	border: 1px solid #dadada;
	overflow: auto;
}

.wea-locale-provider-set .wea-locale-provider-set-trans-btn {
	position: absolute;
	top: 50%;
	left: 50%;
	margin: -50px 0 0 -14px;
	height: 100px;
	width: 28px;
	font-size: 28px;
}

.wea-locale-provider-set .wea-locale-provider-set-trans-btn i {
	cursor: pointer;
	color: #2db7f5;
}

.wea-locale-provider-set .wea-locale-provider-set-trans-btn i:hover {
	color: #57c5f7;
}



